import api from '../../common/api.js';
export default {
	onLoad(option) {
		this.id = option.id
		this.communityId = option.communityId
		this.getMyhvideo(this.id)
		if(this.communityId){
			this.getCommunityByIdOmit(this.communityId)	
		}
	},
	data() {
		return {
			id: '',
			formData: {},
			communityId: '',
			communityInfo: {}
		}
	},
	methods: {
		getMyhvideo(id) {
			// 获取关于我的直播
			api.getMyhVideo({
				id: id
			}).then(res=>{
				this.formData = res.data
			})
		},
		getCommunityByIdOmit (id) {
			this.$api.getCommunityByIdOmit({
				id: id
			}).then(res => {
				if(res.status=="OK"){
					this.communityInfo = res.data
				}
			})
		},
		videoErrorCallback: function(e) {
			uni.showModal({
				content: e.target.errMsg,
				showCancel: false
			})
		},
		onShareAppMessage () {
			return {
				title: this.formData.title,
				path: `pages/liveborad/share?id=${this.formData.id}&communityId=${this.communityId}`
			}
		}
	}
}
